import React from 'react';
import styled from 'styled-components';

const Available = styled.a`
  color: #85d0a1;

  &:after {
    border-bottom-color: #43ac6a;
  }
  &:hover {
    color: #43ac6a;
  }
  &:hover {
    &:after {
      border-bottom-color: #43ac6a;
    }
  }
`;
const Unavailable = styled.a`
  color: #f79483;

  &:after {
    border-bottom-color: #f04124;
  }
  &:hover {
    color: #f04124;
  }
  &:hover {
    &:after {
      border-bottom-color: #f04124;
    }
  }
`;

const WorkStatus = ({ availability }) => {
  if (availability === true) {
    return <Available href="why-me.php">available for freelance opportunities</Available>;
  }
  return <Unavailable href="why-me.php">unavailable for freelance opportunities</Unavailable>;
};

export default WorkStatus;



