import React, { Component } from 'react';
import styled from 'styled-components';
import H1 from '../../components/H1/H1';
import Header from '../../components/Header/Header';
import Footer from '../../components/Footer/Footer';
import H3 from '../../components/H3/H3';

const AboutPage = styled.div`
  
  color: #dcdcdd;

  h1,
  h2,
  h3,
  h4,
  h5,
  h6 {
    color: #dcdcdd;
  }
  a {
    color: #a8a8ab;

    &:after {
      border-color: #a8a8ab;
    }
    &:hover {
      color: #a8a8ab;
      &:after {
        border-color: #a8a8ab;
      }
    }
  }

  .logo {
    a {
      border-color: #dcdcdd;
      animation: none;
    }
    path {
      fill: #dcdcdd;
      animation: none;
    }
  }
  footer {
    background: #c5c3c6;
    a {
      color: #4c5c68;
      &:after {
        border-color: #4c5c68;
      }
      &:hover {
        color: #1985a1;
        &:after {
          border-color: #1985a1;
        }
      }
    }
  }
`;

export default class About extends Component {
  componentDidMount() {
    setTimeout(() => {
      document.getElementsByTagName('body')[0].classList = 'loaded about';
    }, 500);
  }

  componentWillUnmount() {
    document.getElementsByTagName('body')[0].classList = 'about';
  }


  render() {
    return (
      <AboutPage>
        <Header />
        <main className="container align-top mb-1">
          <div className="sm-col-12 sm-text-align-center">
            <H1 className="sm-text-align-center uppercase large-text js-animate fadeIn animate-2">
              I  to make things for the Internet
            </H1>
          </div>

          <div className="sm-col-12 lg-col-4  js-animate fadeIn animate-3">
            <img src="http://placehold.it/400x400" />
          </div>
          <div className="sm-col-12 lg-col-8 story-text  js-animate fadeIn animate-3">
            <p className="mt-0">
              I have over 10 years industry experience working in-house and for
              agencies. I have made award winning projects for clients like
              VisitEngland, AO, Microsoft, BAE and Nikon
            </p>

            <p>
              Whilst originally a PHP Developer, I have recently become more
              involved with Front end development using modern day tools. This
              has allowed me to have a great in-depth understanding of the whole
              web design process, from wireframe up to deployment.
            </p>

            <p>
              Away from web development, I'm a keen PS4 gamer, avid Netflix
              watcher, and love spending as much time with my wife and two sons.
            </p>
          </div>

          <div className="sm-col-12 md-col-6 lg-col-3 js-animate fadeIn animate-3">
            <h3 className="underline">Back end</h3>
            <ul className="mt-0 listless  serif">
              <li>PHP 5+</li>
              <li>CakePHP</li>
              <li>MySQL</li>
              <li>Wordpress</li>
            </ul>
          </div>
          <div className="sm-col-12 md-col-6 lg-col-3 js-animate fadeIn animate-3">
            <h3 className="underline">Front End</h3>

            <ul className="mt-0 listless serif">
              <li>HTML 5</li>
              <li>Javascript / jQuery</li>
              <li>CSS / Sass</li>
              <li>Angular JS</li>
              <li>React</li>
            </ul>
          </div>
          <div className="sm-col-12 md-col-6 lg-col-3 js-animate fadeIn animate-3">
            <h3 className="underline">Server side</h3>

            <ul className="mt-0 listless serif">
              <li>Node.js</li>
              <li>GIT</li>
              <li>AWS</li>
            </ul>
          </div>
          <div className="sm-col-12 md-col-6 lg-col-3 js-animate fadeIn animate-3">
            <h3 className="underline">Everything else</h3>

            <ul className="mt-0 listless serif">
              <li>Photoshop</li>
              <li>Illustrator</li>
              <li>UXPin</li>
            </ul>
          </div>

          <div className="sm-col-12 md-col-12 js-animate fadeIn animate-3">
            <H3 className="underline">Want to talk?</H3>
            <p>
              I am always interested in listening to any design or development
              opportunities, big or small, so feel free to{' '}
              <a href="#">email me</a> or catch me on <a href="#">Twitter</a>.
            </p>
          </div>
        </main>
        <Footer />
      </AboutPage>
    );
  }
}



