import React, { Component } from 'react';
import styled from 'styled-components';
import H1 from '../../components/H1/H1';
import H2 from '../../components/H2/H2';
import WorkStatus from '../../components/WorkStatus/WorkStatus';
import SocialBar from '../../components/SocialBar/SocialBar';
import Header from '../../components/Header/Header';
import Footer from '../../components/Footer/Footer';
import WorkThumb from '../../components/WorkThumb/WorkThumb';

const HomePage = styled.div`
  
  footer {
    background: #4c5c68;
    color: #dcdcdd;

    a {
      color: #dcdcdd;
      &:after {
        border-color: #dcdcdd;
      }
      &:hover {
        color: #dcdcdd;
        &:after {
          border-color: #dcdcdd;
        }
      }
    }
  }
`;

export default class Home extends Component {
  constructor(props) {
    super(props);
    this.state = { available: true };
  }

  componentDidMount() {
    setTimeout(() => {
      document.getElementsByTagName('body')[0].classList = 'loaded home';
    }, 500);
  }

  componentWillUnmount() {
    document.getElementsByTagName('body')[0].classList = 'home';
  }


  render() {
    return (
      <HomePage>
        <Header />
        <main>
          <div className="container align-center mb-1">
            <div className="sm-col-12 sm-text-align-left">
              <H1 className="js-animate fadeIn animate-2 mt-0">
                I am a full stack Web Developer from Preston, UK.{' '}
                <span className="js-animate fadeIn animate-3">
                  I currently work for{' '}
                  <a
                    href="https://www.skiddle.com"
                    rel="noopener noreferrer"
                    target="_blank"
                  >
                    Skiddle
                  </a>
                </span>
              </H1>
              <H2 className="js-animate fadeIn  animate-5">
                I am <WorkStatus availability={this.state.available} />
              </H2>
              <SocialBar />
            </div>
          </div>
          <div class="container js-animate fadeIn animate-5 mb-1">
          <WorkThumb />
          <WorkThumb />
          <WorkThumb />
          <WorkThumb />
          <WorkThumb />
          <WorkThumb />
          </div>
        </main>
        <Footer />
      </HomePage>
    );
  }
}



