import React from 'react';
import styled from 'styled-components';
import { Link } from 'react-router-dom';
import logo from './logo.svg';

const LogoBox = styled.div`
  display: inline-grid;

  img {
    width: 51px;
    heigth: 21px;
  }

  path {
    fill: #46494c;
    animation: colorFill 30s infinite;
    transition: fill cubic-bezier(0.68, 0, 0.265, 1) 0.25s;
  }

  a {
    display: inherit;
    padding: 25px 10px;
    border: 4px solid #46494c;
    animation: colorBG 30s infinite;
    transition: background-color cubic-bezier(0.68, 0, 0.265, 1) 0.25s;

    &:after {
      display: none;
    }

    &:hover {
      background: #46494c;
      -webkit-animation: none;
      animation: none;

      path {
        fill: #dcdcdd;
        -webkit-animation: none;
        animation: none;
      }
    }
  }

  @keyframes colorBG {
    0% {
      border-color: #46494c;
    }
    33% {
      border-color: #4c5c68;
    }
    66% {
      border-color: #1985a1;
    }
    100% {
      border-color: #46494c;
    }
  }
  @keyframes colorFill {
    0% {
      fill: #46494c;
    }
    33% {
      fill: #4c5c68;
    }
    66% {
      fill: #1985a1;
    }
    100% {
      fill: #46494c;
    }
  }
`;

const Logo = () => (
  <LogoBox>
    <Link to="/" data-theme-change="home">
      <img src={logo} alt="michael-smedley.co.uk" width="51" height="21" />
    </Link>
  </LogoBox>
);

export default Logo;



