import styled from 'styled-components';

const H1 = styled.h1`
  font-family: 'gt_eesti_display', 'Helvetica Neue', 'Helvetica', 'Arial',
    sans-serif;
  color: #46494c;

  @media (min-width: 64rem) {
    font-size: 3.125rem;
  }
`;

export default H1;



