import React, { Component } from 'react';
import styled from 'styled-components';
import H1 from '../../components/H1/H1';
import H2 from '../../components/H2/H2';
import Header from '../../components/Header/Header';
import Footer from '../../components/Footer/Footer';

const WorkPage = styled.div`
  
  h1,
  h2,
  h3,
  h4,
  h5,
  h6 { 
    color: #dcdcdd;
  }
  a {
    color: #a8a8ab;

    &:after { 
      border-color: #a8a8ab; 
    }

    &:hover{
      color: #a8a8ab;

      &:after{
        border-color: #a8a8ab;
      }
    }
  }

  .logo {
    a{
      border-color: #dcdcdd;
      -webkit-animation: none;
      animation: none;
    }

    path {
      fill: #dcdcdd; -webkit-animation: none; animation: none;
    }
  }

  footer {
    background: #c5c3c6;

    a {
      color: #4c5c68;

      &:after {
        border-color: #4c5c68;
      }

      &:hover {
        color: #1985a1;

        &:after{
          border-color: #1985a1;
        }
      }
    }
  }
`;

export default class Work extends Component {
  componentDidMount() {
    setTimeout(() => {
      document.getElementsByTagName('body')[0].classList = 'loaded work';
    }, 500);
  }

  componentWillUnmount() {
    document.getElementsByTagName('body')[0].classList = 'work';
  }


  render() {
    return (
      <WorkPage>
        <Header />
        <div className="container mb-1">
  <div className="sm-col-12 js-animate fadeIn slideUp animate-2">
    <H1 className="sm-text-align-center uppercase large-text">Work Item</H1> 
  </div>
  <div className="sm-col-12 js-animate fadeIn slideUp animate-3">
    <img src="http://placehold.it/1920x1080" />
  </div>
  <div className="sm-col-12 js-animate fadeIn slideUp animate-4">
    <p><a href="http://linktoproject.com" rel="nofollow noopener">http://linktoproject.com</a></p>
    <H2>Project Brief</H2>
    <p>Here we put in a description of what the point and goals of the project were</p>

    <H2>Implementation</H2>
    <p>What I did to implement stuff</p>

    <H2>Results</H2>
    <p>Any goals and results that are worth mentioning</p>

    <H2>Tools used</H2>
    <p>Basically languages etc.  Can we tag this through API?</p>


  </div>

  

  <div className="sm-col-12 md-col-12 js-animate fadeIn slideUp animate-5">
    <h3 className="underline">Does this sound like you?</h3>
    <p>I am always interested in listening to any design or development opportunities, big or small, so feel free to <a href="#">email me</a> or catch me on <a href="#">Twitter</a>.</p>
  </div>
</div>
        <Footer />
      </WorkPage>
    );
  }
}



